body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face{
  font-family: "icon-turn-arrow";
  src: url(/hook-test/static/media/turn-arrow.67bdc0ef.eot) format("embedded-opentype"),
      url(/hook-test/static/media/turn-arrow.00d17ac5.woff) format("woff"),
       url(/hook-test/static/media/turn-arrow.8efc6f72.ttf) format("truetype"),
        url(/hook-test/static/media/turn-arrow.8b9fa981.svg) format("svg");
}

html, body{
  width: 100%;
  height: 100%;
  background-color: #222; 
}
/* 设置舞台的样式 */
.stage{
 width: 100%;
 height: 100vh;
 position: relative;
 overflow: hidden;
 background-color: rgb(224, 198, 198); 
 
 /* margin:0 auto; */
 /* border: 5px solid black; */
}

/* 单个图片显示组件相关的设置 */
.image-figure{
  position: absolute;
  width: 320px;
  height: 360px;
  margin: 0;
  padding: 40px;
  background-color: #fff;
  box-sizing: border-box;

   /*小手*/
   cursor: pointer;

  /*  移动相关过渡效果  */
  -webkit-transform-origin: 0 50% 0;
          transform-origin: 0 50% 0;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
  transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
  transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;
  transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
  
}

/* 图片的反面 */
.img-back{
  position: absolute;
  top:0;
  left: 0;

  width: 100%;
  height: 100%;
  padding: 50px 40px;

  background-color: #fff;

  color: #a7a0a2;
  font-size: 22px;
  line-height: 1.25;
  text-align: left;

  box-sizing: border-box;
  -webkit-transform: rotateY(180deg) translateZ(1px);
          transform: rotateY(180deg) translateZ(1px);/*图片反转后 文字是镜像*/
  -webkit-backface-visibility:hidden;
          backface-visibility:hidden;
}
/* 图片下面的标题 */
.img-title{
  margin:20px 0 0 0;
  color:#a7a0a2;
  font-size: 16px;
}
/* 反转 */
.is-inverse {
  -webkit-transform: translate(320px) rotateY(180deg);
          transform: translate(320px) rotateY(180deg);
}


.controller-nav{
  position: absolute;
  left: 0;
  bottom: 30px;
  z-index: 101;

  width: 100%;
  text-align: center;
}

.controller-unit{
  display: inline-block;
  margin: 0 5px;
  width: 30px;
  height: 30px;
  text-align: center;
  
  cursor: pointer;
  background-color: #aaa;
  border-radius: 50%;

  -webkit-transform: scale(0.5);

          transform: scale(0.5);

  vertical-align: middle;

  -webkit-transition: background-color 0.3s ,-webkit-transform 0.6s ease-in-out;

  transition: background-color 0.3s ,-webkit-transform 0.6s ease-in-out;

  transition: transform 0.6s ease-in-out ,background-color 0.3s;

  transition: transform 0.6s ease-in-out ,background-color 0.3s ,-webkit-transform 0.6s ease-in-out;
}

.is-center{
  background-color: #888;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.is-center:after{
font-size: 110%;
line-height: 30px;
font-family: "icon-turn-arrow";
content: "\E600";

-webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
}

.isInverse{
        background-color: #555;

        -webkit-transform:rotateY(180deg);

                transform:rotateY(180deg);
      }
